.view-collection-representative-object {
  .field--item {
    position: relative;

    a {
      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: $collection-header-bg;
        opacity: .8;
      }
    }
  }
}
